* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.flex {
    display: flex;
}

.inline-flex {
    display: inline-flex;
}

.inline-table {
    display: inline-table;
}

.center {
    text-align: center;
    justify-content: center;
    align-items: center;
}

.between {
    justify-content: space-between;
}

.column {
    flex-flow: column;
}

.full-height {
    height: 100%;
}

.full-width {
    width: 100%;
}

.relative {
    position: relative;
}

.clickable {
    cursor: pointer;
}
h1, h2, h3, h4, h5, h6, pre {
    font-family: 'Courier New';
    font-weight: bold;
}

div, p, button, span, ul, li, .marker, textarea {
    font-family: 'Inconsolata';
}

body {
    background: #FCFFFF;
}

h1, h2, h3, h4, h5, h6, button, a {
    color: #1745AD;
}

p, pre, ul, li, textarea {
    color: #305EC6;
}

::selection {
    background: #C2F0FF;
}

::-moz-selection {
    background: #C9F7FF;
}

.active {
    color: #6391F9;
}

.logo {
    background: #96C4FF;
}

.title {
    color: #fefefe;
}

.title::selection {
    background: #305EC6;
}

.title::-moz-selection {
    background: #305EC6;
}

.brackets {
    color: #efefff;
}

article {
    background-color: #EFFFFF;
    border-top: 3px solid #305EC6;
    border-bottom: 3px solid #305EC6;
}

@media (hover: hover) {
    .clickable:hover:not(.header) {
        color: #6391F9;
    }
}
h1 {
    font-size: 5vh;
}

button, pre {
    font-size: 4.5vh;
}

p, ul, li, textarea {
    font-size: 2.75vh;
}

ul {
    list-style: none;
}

textarea {
    height: 1.5em;
    resize: none;
}

div.br {
    height: calc(2.75vh / 2);
}

header {
    margin-top: 7.5vh;
    margin-bottom: 2.5vh;
}

.nav {
    height: 4vh;
    justify-content: space-evenly;
}

.navpage {
    width: 25%;
}

.logo {
    height: calc((4vh * 5) + 30px);
    bottom: 6vh;
    border-radius: 100%;
    z-index: 1;
    padding: 20px;
}

.brackets {
    font-size: calc(4vh * 3.75);
    font-weight: normal;
    width: 40px;
}

.brackets, textarea {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.title-1, .marker pre {
    font-size: 2.5vh;
}

.title-2, h2 {
    font-size: 3.5vh;
}

.title-3 {
    font-size: 4.25vh;
}

button, textarea {
    background: none;
    border: none;
    outline: none;
}

article {
    padding: 50px 30px 35px 50px;
}

.img-content, .text-content {
    width: 50%;
}

.content, .text-content, textarea {
    line-height: 1.5;
}

.text-content {
    padding: 0 25px 0 75px;
}

.text-content * {
    margin-bottom: 10px;
}

.marker, .marker pre {
    margin-bottom: 0px;
}

.marker pre {
    align-self: flex-start;
}

.heading, .underline {
    text-decoration: underline;
}

.heading, .footnote {
    font-weight: bold;
}

.footnote {
    font-style: italic;
}

.project {
    height: -webkit-max-content;
    height: max-content;
}

.project * {
    margin-bottom: 0;
}

.project p {
    display: inline-block;
    margin-bottom: 5px;
}

.float-r {
    margin-right: 15px;
}

.source {
    height: 30vh;
}

.source * {
    border: 1px solid black;
}

.contact, .contact iframe {
    min-width: 100%;
    min-height: 905px;
    max-height: 1105px;
    overflow: visible;
}

.contact iframe {
    min-height: inherit;
    border: 0px solid black;
}

footer {
    padding: 30px 50px;
}

td {
    width: 12.5vh;
    margin-right: 10vh;
    white-space: nowrap;
    text-align: left;
}

.td-long {
    width: 16.5vh;
}

.link-row * {
    font-size: 2vh;
}

.socials a {
    height: 8vh;
    margin: 0 10px;
}

.socials a img {
    height: 100%;
    width: auto;
}

.space {
    margin-bottom: 20px;
}

a {
    text-decoration: none;
}
@media (orientation: portrait) {
    header {
        margin-top: 2.5vh;
    }

    .navpage, .img-content, .text-content {
        width: auto;
        width: initial;
    }

    .content {
        display: block;
    }
    
    .logo {
        display: none;
    }

    button, pre {
        font-size: 2.75vh;
    }

    h2 {
        font-size: 3vh;
    }

    p, ul, li, textarea {
        font-size: 2.5vh;
    }
    
    div.br {
        height: calc(2.5vh / 2);
    }

    .mobile-br {
        padding-bottom: 2vh;
    }

    article {
        padding: 25px 15px 27.5px 25px;
    }

    .img-content {
        margin-bottom: 2vh;
    }

    .text-content {
        padding: 0;
    }

    footer {
        padding: 10px 15px;
    }

    .socials {
        justify-content: space-between;
        width: auto;
    }

    .socials a {
        margin: 0 5px;
    }
}

@media (orientation: portrait) and (max-width: 400px) {
    .socials {
        flex-flow: column;
    }

    .socials a {
        height: 6vh;
        margin: 5px 0;
    }
}

@media (orientation: portrait) and (max-width: 600px) {
    td {
        width: auto;
        width: initial;
    }
    
    tbody {
        display: flex;
        padding-top: 10px;
    }

    tr.flex {
        display: inline-flex;
        flex-flow: column;
        width: -webkit-min-content;
        width: min-content;
        margin-right: 3vh;
        margin-bottom: 0;
    }

    td, .td-long {
        width: -webkit-min-content;
        width: min-content;
        margin-right: 0;
        margin-bottom: 15px;
    }

    .socials a {
        height: 7.25vh;
    }
}

@media (orientation: portrait) and (min-width: 600px) {
    footer {
        padding: 17.5px 26.25px;
    }

    .img-content {
        margin-top: 1vh;
    }

    img.full-width {
        width: 80%;
        margin: 0 10%;
    }

    td {
        margin-right: 3.5vh;
    }

    .socials a {
        height: 6.5vh;
    }
}

@media (orientation: portrait) and (min-height: 1300px) {
    td {
        width: auto;
        width: initial;
    }
    
    tbody {
        display: flex;
        padding-top: 10px;
    }

    tr.flex {
        display: inline-flex;
        flex-flow: column;
        width: -webkit-min-content;
        width: min-content;
        margin-right: 3vh;
        margin-bottom: 0;
    }

    td, .td-long {
        width: -webkit-min-content;
        width: min-content;
        margin-right: 0;
        margin-bottom: 15px;
    }
}

@media (orientation: portrait) and (min-height: 2000px) {
    article {
        padding: 75px 45px 82.5px 75px;
    }

    footer {
        padding: 40px 60px;
    }
}

@media (orientation: landscape) and (hover: none) {
    .link-row * {
        font-size: 2.5vh;
    }
}

@media (min-width: 0px) and (max-width: 400px) {
    .contact, .contact-iframe {
        min-height: 1105px;
    }
}

@media (max-width: 518px) {
    textarea {
        height: 4.5em;
    }
}

@media (min-width: 400px) and (max-width: 650px) {
    .contact, .contact-iframe {
        min-height: 1005px;
    }
}

@media (orientation: landscape) and (max-width: 900px) {
    button, pre {
        font-size: 3.5vh;
    }
}

@media (min-width: 518px) and (max-width: 1110px) {
    textarea {
        height: 3em;
    }
}

@media (min-width: 1450px) {
    button, pre {
        font-size: 3.75vh;
    }
}
